.main-header {
  background-color: #fff;
  width: 1150px;
  margin: 0 auto;
  outline: 1px solid red;
}

.main-nav__list,
.user-nav__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.main-nav__link,
.user-nav__item {
  font-family: 'Open Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
